

























caret = (cb)-> ->
  { input } = @$refs
  st = input.selectionStart
  ed = input.selectionEnd
  value = @value
  after = cb value[...st], value[st...ed], value[ed...]
  size = after.length - value.length
  @$emit 'input', after
  @$nextTick ->
    input.selectionStart = st
    input.selectionEnd = ed + size

module.exports =
  default:
    props:
      value:
        type: String
        required: true
      maxSize:
        type: Number
        default: 100
      maxRow:
        type: Number
        default: 1
      minRow:
        type: Number
        default: 1

    data: ->
      caret: {}

    methods:
      nDm:     caret (pre, select, post)-> "#{pre}[[#{select}]]#{post}"
      anker:   caret (pre, select, post)-> "#{pre}>>#{select}#{post}"
      comment: caret (pre, select, post)-> "#{pre}/*#{select}*/#{post}"

      submit: ->
        console.log @value

      input: (e)->
        @$emit 'input', e.target.value

    computed:
      ban: ->
        ban = false
        ban ||= @maxSize < @size
        ban ||= @row < @minRow
        ban ||= @maxRow < @row
        ban
      warn: ->
        ok = true
        ok &&= ! @value.match ///>>///
        ! ok
      mark: ->
        m = "fa-check"
        m = "fa-warning" if @warn
        m = "fa-ban"     if @ban
        [m]

      size: ->
        @value.length
      row: ->
        @value.split("\n").length
      areaRow: ->
        return @minRow if @row < @minRow
        return @maxRow if @maxRow < @row
        return @row




