
_ = require "lodash"

module.exports =
  props: ["as", "value"]
  methods:
    tap: ->
      idx = @value.indexOf(@as)
      newVal =
        if idx < 0
          [@as, @value...]
        else
          [@value[0...idx]...,@value[idx + 1..-1]...]
      @$emit 'input', newVal


  computed:
    btn: ->
      idx = @value.indexOf(@as)
      bool = !(idx < 0)
      if bool
        ["active"]
      else
        []



