







































































{ Query } = require "~plugins/memory-record"

module.exports =
  props: ["value"]
  data: ->
    sort: "live"
    order: "asc"
    full_mode: true
    live_mode: true
  watch:
    value: ->
      for o in @potofs
        o.hide = false
      for id in @value
        Query.potofs.find(id).hide = true
      @$store.commit "book/data", {}

  computed:
    full_on:  ->  @potof_ids -> false
    full_off: ->  @potof_ids -> true
    live_on:  ->  @potof_ids (o)-> ! o.commit
    live_off: ->  @potof_ids (o)-> o.commit

    part: ->
      { potof } = @$store.state.menu.set
      potof && @$parent.part

    potofs: ->
      { book_id, part_id } = @$parent
      { sort, order } = @
      if book_id
        Query.potofs.catalog(book_id, part_id, sort, order).list
      else
        []

    bgc: ->
      switch @sort
        when "text", "role_labels", "winner_id", "win"
          (o)-> o.winner_id
        else
          (o)-> o.live.role_id

  methods:
    potof_ids: (f)->
      @potofs
      .filter f
      .map (o)-> o.id
      .sort()

    reset: (as)->
      @$emit 'input', as.sort()

    toggle: (o)->
      o.hide = ! o.hide
      @$emit 'input', @potof_ids (o)-> o.hide

    reverse: ->
      switch @order
        when "asc"
          @order = "desc"
        when "desc"
          @order = "asc"

    count: (unit, n)->
      switch n
        when 0, undefined, null, Infinity
          " "
        else
          "#{n}#{unit}"




