

module.exports =
  default:
    props: ["top"]
    data: ->
      text: ""

    computed:
      text_rows: ->
        rows = @text.split("\n").length
        if 2 < rows
          rows
        else
          3

      show_write: ->
        @$store.state.menu.target == 'comment'
      show: ->
        @show_write
      style: ->
        position: "absolute"
        transform: "translateY(#{@top}px)"




