
_ = require "lodash"
el = require "~plugins/dom"

module.exports =
  props: ["as", "value"]

  computed:
    el_adjust: el.adjust
    btn: ->
      if window? && @el_adjust
        requestAnimationFrame =>
          @$emit 'input', @as
      [@el_adjust]



