
{ Query } = require "~plugins/memory-record"

module.exports =
  props:
    face_id:
      type: String
    hide:
      type: Boolean
  computed:
    image_class: ->
      if @hide
        ["hide"]
      else
        []
    has_html: ->
      !! @$slots.default
    face_url: ->
      Query.faces.find(@face_id, "all")?.path
  methods:
    click: ->
      @$emit 'click', @face_id





